<template>
	<transition name="move">
		<div v-show="showFlag" class="food" ref="food">
			<div class="food-content">
				<div class="image-header">
					<img :src="food.image" />
					<div class="back" @click="hide">
						<span class="icon-arrow_lift"></span>
					</div>
				</div>
				<div class="content">
					<h1 class="food-title" v-text="food.name"></h1>
					<div class="food-detail">
						<span class="sell-count" v-text="'月售' + food.sellCount + '份'"></span>
						<span class="rating" v-text="'好评率' + food.rating + '%'"></span>
					</div>
					<div class="food-price">
						<span class="now" v-text="'￥'+food.price"></span>
						<span class="old" v-show="food.oldPrice" v-text="'￥'+food.oldPrice"></span>
					</div>
					<div class="cartcontrol-wrapper">
			            <cartcontrol  @add="addFood" :food="food"></cartcontrol>
			        </div>
			        <transition name="fade">
			        	<div @click.stop.prevent="addFirst" class="buy" v-show="!food.count || food.count===0">加入购物车</div>
			        </transition>
				</div>
				<split v-show="food.info"></split>
				<div class="info" v-show="food.info">
					<h1 class="title">商品信息</h1>
					<p class="text" v-text="food.info"></p>
				</div>
				<split v-show="food.info"></split>
				<div class="rating">
					<h1 class="title">商品评价</h1>
					<ratingselect @select="selectRating" @toggle="toggleContent" :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings"></ratingselect>
					<div class="rating-wrapper">
						<ul v-show="food.ratings && food.ratings.length">
							<li v-show="needShow(rating.rateType,rating.text)" v-for="rating of food.ratings" class="rating-item border-1px">
								<div class="user">
									<span class="name" v-text="rating.username"></span>
									<img :src="rating.avatar" width="12" height="12" class="avatar" />
								</div>
								<div class="time">{{rating.rateTime | formatDate}}</div>
								<p class="text">
									<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
								</p>
							</li>
						</ul>
						<div class="no-rating" v-show="!food.ratings || !food.ratings.length">暂无评价</div>
					</div>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	import Vue from 'vue'
	import {formatDate} from '@/common/js/date'
	import BScroll from 'better-scroll'
	import cartcontrol from '@/components/cartcontrol/cartcontrol.vue'
	import split from '@/components/split/split.vue'
	import ratingselect from '@/components/ratingselect/ratingselect.vue'
	
	const POSITIVE = 0;
	const NEGATIVE = 1;
	const ALL = 2;
	
	export default {
		 props: {
		      food: {
		        type: Object
		      }
		},
		data() {
			return {
				showFlag: false,
				selectType: ALL,
				onlyContent: true,
				desc: {
					all: '全部',
					positive: '推荐',
					negative: '吐槽'
				}
			};
		},
		methods: {
			show() {
				this.showFlag = true;
				this.selectType = ALL;
				this.onlyContent = true;
				this.$nextTick(()=>{
					if(!this.scroll) {
						this.scroll = new BScroll(this.$refs.food,{
							click:true
						});
					}else{
						this.scroll.refresh();
					}
				});
			},
			hide() {
				this.showFlag = false;
			},
			addFirst(event) {
		        if (!event._constructed) {
		          return;
		        }
		        this.$emit('add', event.target);
		        Vue.set(this.food, 'count', 1);
		     },
			addFood(target) {
		        this.$emit('add', target);
		    },
		    needShow(type,text) {
		   		if(this.onlyContent && !text) {
		   			return false;
		   		}
		   		if(this.selectType === ALL) {
		   			return true;
		   		}else{
		   			return type === this.selectType;
		   		}
		    },
		    selectRating(type) {
		    	this.selectType = type;
		    	this.$nextTick(()=>{
		    		this.scroll.refresh();
		    	});
		    },
		    toggleContent() {
		    	this.onlyContent = !this.onlyContent;
		    	this.$nextTick(()=>{
		    		this.scroll.refresh();
		    	});
		    }
		    
		},
		filters: {
			formatDate(time) {
				let date = new Date(time);
				return formatDate(date, 'yyyy-MM-dd hh:mm');
			}
		},
		components: {
			cartcontrol,
			split,
			ratingselect
		}
	}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin.styl"
	.food
		position: fixed
		width: 100%
		left: 0
		top: 0
		bottom: 48px
		z-index: 30
		background: #fff
		transform: translate3d(0,0,0)
		&.move-enter-active,&.move-leave-active
			transition: all .2s linear
		&.move-enter,&.move-leave-active
			transform: translate3d(100%,0,0)
		.image-header
			position: relative
			width: 100%
			height: 0
			padding-top: 100%
			img
				position: absolute
				left: 0
				top: 0
				width: 100%
				height: 100%
			.back
				position: absolute
				top: 10px
				left: 0
				.icon-arrow_lift
					display: block
					padding: 10px
					font-size: 20px
					color: #fff
		.content
			display: block
			position: relative
			padding: 18px
			margin: 0
			.food-title
				line-height: 14px
				margin-bottom: 8px
				font-size: 14px
				font-weight: 700
				color: rgb(7,17,27)
			.food-detail
				margin-bottom: 18px
				line-height: 10px
				font-size: 0
				.sell-count,.rating
					font-size: 10px
					color: rgb(147,153,159)
				.sell-count
					margin-right: 12px
			.food-price
				font-weight: 700
				line-height: 24px
				.now
					margin-right: 8px
					font-size: 14px
					color: rgb(240, 20, 20)
				.old
					text-decoration: line-through
					font-size: 10px
					color: rgb(147, 153, 159)
			.cartcontrol-wrapper
				position: absolute
				right: 12px
				bottom: 12px
			.buy
				position: absolute
				right: 18px
				bottom: 18px
				z-index: 10
				height: 24px
				line-height: 24px
				padding: 0 12px
				box-sizing: border-box
				border-radius: 12px
				font-size: 10px
				color: #fff
				background: rgb(0, 160, 220)
				opacity: 1
				&.move-enter-active,&.move-leave-active
					transition: all .3s
				&.move-enter,&.move-leave-active
					opacity: 0
		.info
			padding: 18px
			.title
				line-height: 14px
				margin-bottom: 6px
				font-size: 14px
				color: rgb(7,17,27)
			.text
				line-height: 24px
				padding: 0 8px
				font-size: 12px
				color: rgb(77,85,93)
		.rating
			padding-top: 18px
			.title
				line-height: 14px
				margin-left: 18px
				font-size: 14px
				color: rgb(7,17,27)
			.rating-wrapper
				padding: 0 18px
				.rating-item
					position: relative
					padding: 16px 0
					border-1px(rgba(7,17,27,0.1))
					.user
						position: absolute
						line-height: 12px
						right: 0
						top: 16px
						font-size: 0
						.name
							display: inline-block
							margin-right: 6px
							vertical-align: top
							font-size: 10px
							color: rgb(147,153,159)
						.avatar
							border-radius: 50%
					.time
						margin-bottom: 6px
						line-height: 12px
						font-size: 10px
						color: rgb(147,153,159)
					.text
						line-height: 16px
						font-size: 12px
						color: rgb(7,17,27)
						.icon-thumb_up,.icon-thumb_down
							margin-right: 4px
							line-height: 24px
							font-size: 12px
						.icon-thumb_up
							color: rgb(0,160,220)
						.icon-thumb_down
							color: rgb(147,153,159)
				.no-rating
					padding: 16px 0
					font-size: 12px
					color: rgb(147,153,159)
</style>


